@font-face {
  font-family: "wadi";
  src: url("../../fonts/wadi.eot");
  src: url("../../fonts/wadi.eot") format("embedded-opentype"), url("../../fonts/wadi.ttf") format("truetype"), url("../../fonts/wadi.woff") format("woff"), url("../../fonts/wadi.svg") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}
[class^=wadi-], [class*=" wadi-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: "wadi" !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
[class^=wadi-] .title, [class*=" wadi-"] .title {
  font-family: Roboto, Arial, Helvetica, Verdana, sans-serif;
}

/*
  Make small tag for Wadi Addons
*/
#elementor-panel-elements .elementor-element-wrapper [class^=wadi-]::after,
#elementor-panel-elements .elementor-element-wrapper [class*=" wadi-"]::after {
  content: "WA";
  background-color: #ffffff;
  color: #000000;
  border: 1px solid #000;
  content: "WA";
  border-radius: 8px;
  padding: 3px;
  font-family: Montserrat, Sans-serif;
  font-size: 10px;
  font-weight: 700;
  opacity: 0.4;
  /* padding: 1px; */
  position: absolute;
  right: 0.6em;
  top: 0.6em;
}

